* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  body {
    background: #161a2b;
  }
  .todo-app {
    display: flex;
    flex-direction: column;
    justify-content: start;
    width: 520px;
    min-height: 600px;
    text-align: center;
    margin: 128px auto;
    border-radius: 10px;
    padding-bottom: 32px;
  }
  .header{
    display: flex;
    flex-direction: column;
    margin-bottom: 32px ;


  }
  .header img{
    width: calc(2vw + 30px);
  }
  h1 {
    color: #fff;
    font-size: 24px;
  }
  
  .complete {
    text-decoration: line-through;
    opacity: 0.4;
  }
  
  .todo-form {
    margin-bottom: 32px;
  }
  
  .todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid    rgb(122, 19, 19) ;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
  }
  
  .todo-input::placeholder {
    color: #e2e2e2;
  }
  
  .todo-button {
    padding: 16px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
      90deg,
      rgb(122, 19, 19) 40%,
      rgb(173, 126, 24) 100%
    );
    color: #fff;
    text-transform: capitalize;
  }
  
  .todo-input.edit {
    border: 2px solid #149fff;
  }
  
  .todo-button.edit {
    background: linear-gradient(
      90deg,
      rgb(83, 129, 161) 0%,
      rgba(17, 122, 255, 1) 100%
    );
    padding: 16px 22px;
  }
  
  .todo-container {
    display: flex;
    flex-direction: row;
    position: relative;
  }
  
  .todo-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px auto;
    color: #fff;
    background: linear-gradient(
      90deg,
      rgb(104, 59, 27) 0%,
      rgb(109, 101, 32) 100%
    );
  
    padding: 16px;
    border-radius: 5px;
    width: 90%;
  }
  
  .todo-row:nth-child(3n + 0) {
    background: linear-gradient(
      90deg,
      rgb(30, 61, 16) 0%,
      rgb(104, 112, 30) 100%
   
    );
  }
 
  
  .todo-row:nth-child(3n + 1) {
    background: linear-gradient(
      90deg,
      rgb(10, 53, 83) 0%,
      rgb(55, 115, 117) 100%
    );
  }
  
  .icons {
    display: flex;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
  }
  
  .delete-icon {
    margin-right: 5px;
    color: #fff;
  }
  
  .edit-icon {
    color: #fff;
  }

  .remove_button{
    font-size: larger;
    padding: 10px;
    background: linear-gradient(
      90deg,
      rgb(114, 12, 21) 40%,
      rgb(190, 89, 30) 100%
     
    );
    border:none;
    border-radius: 5px;
    color: #fff;
    margin-top: 15px;
  }

  .remove_button:hover{
    background:#88330c ;
  }